import React, { useState } from 'react'
import ReactDOM from 'react-dom'
import { Button, Col, Container, Row, Image, Modal, OverlayTrigger, Popover, ButtonGroup, Badge } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import projects from '../data/projects'
import { Magnifier, MOUSE_ACTIVATION, TOUCH_ACTIVATION } from "react-image-magnifiers";

const Projects = () => {
    const [show, setShow] = useState(false)
    const [pIndex, setPIndex] = useState(null)
    const [project, setProject] = useState(null)

    const [allowZoom, setAllowZoom] = useState(false)

    return (
        <Container fluid id="projects" className="pb-5 pt-60 px-4 mb-4">
            {/* <Button href="/" className="btn-light mb-3 p-0"><i className="fas fa-arrow-left"></i> Go Back</Button> */}
            <h1>Projects</h1>
            <Row className="justify-content-center">
                {projects.map((project, index) => (
                    <Col lg={6} md={6} sm={10} xs={12} id={project._id} className="p-4">
                        {/* <a href={`/project/${project._id}`} style={{textDecoration : 'none'}}> */}
                        <div onClick={() => { setShow(true); setPIndex(index); setProject(project) }}data-aos="flip-up" data-aos-once="true" className="card mb-3 project-card" style={{background : project.backgroundColor, color : project.textColor}}>
                            <div className="card-body">
                                <p className="project-card-text">{project.span}</p>
                                <h4 className="project-card-text card-title txt-medium txt-no-transform">{project.name} - {project.tagLine}</h4>
                                <Image src={project.image} fluid/>
                            </div>
                        </div>
                        {/* </a> */}
                    </Col>
                ))}
                <Modal
                    id="projectModal"
                    size="xl"
                    show={show}
                    onHide={() => {setShow(false); setPIndex(null)}}
                    // dialogClassName="modal-90w"
                    aria-labelledby="project-modal"
                    centered
                >
                    {pIndex !== null &&
                    <>
                    {/* <Modal.Header closeButton> */}
                        {/* <h1 id="project-modal" className="txt-medium txt-no-transform">{project.name} - {project.tagLine}</h1> */}
                        {/* <Modal.Title id="project-modal"> */}
                            {/* {project.name} - {project.tagLine} */}
                        {/* </Modal.Title> */}
                    {/* </Modal.Header> */}
                    <Modal.Body className="p-2">
                        <Button className="btn-light float-right" onClick={() => setShow(false)}>
                            <i className="fas fa-times fa-2x"></i>
                            {/* <p aria-hidden="true"><i className="fas fa-times fa-2x"></i> Close</p> */}
                            {/* <span class="sr-only">Close</span> */}
                        </Button>
                        <Container fluid className="pb-1 px-2 mb-4">

                            <h1 id="projectTitle" className="txt-medium txt-no-transform">{project.name} - {project.tagLine}</h1>

                            <Row className="justify-content-center">
                                <Col xl={6} lg={6} md={6} sm={8} xs={10}>
                                    {project.span && <>
                                    <h6 className="mt-4 text-secondary">Time</h6>
                                    <h5 className="mb-3">{project.span}</h5>
                                    </>}
                                    {project.myRoles.length !== 0 && <>
                                    <h6 className="mt-4 text-secondary">My Roles</h6>
                                    {project.myRoles.map(role => (
                                    <h5>{role}</h5>
                                    ))}
                                    </>}
                                    {project.company && <>
                                    <h6 className="mt-4 text-secondary">Company</h6>
                                    <h5 className="mb-3">{project.company}</h5>
                                    </>}
                                    {project.status && <>
                                    <h6 className="mt-4 text-secondary">Status</h6>
                                    <h5 className="mb-3">{project.status}</h5>
                                    </>}
                                </Col>
                                <Col xl={6} lg={6} md={6} sm={8} xs={10}>
                                    {(project.techStack.length !== 0 || project.techStackImages.length !== 0) && <>
                                    <h6 className="mt-4 text-secondary">Technology Stack</h6>
                                    {project.techStack.map(item => (
                                        <h5>{item}</h5>
                                    ))}
                                    <Row  className="align-items-center m-1 bg-white" style={{maxWidth : 'max-content'}}>
                                    {project.techStackImages.map(item => (
                                        // <Col xl={2} lg={2} md={3} sm={3} xs={4}>
                                            <Image src={item} className="m-1" style={{width : '60px', height : '60px'}} />
                                            
                                        // </Col>
                                    ))}
                                    </Row>
                                    </>}
                                    {project.hostedLink
                                    && <Button href={project.hostedLink} target="_blank" className="custom-colors m-2" style={{background : project.backgroundColor, color : project.textColor}}>Live Demo</Button> 
                                    }
                                    {project.githubRepo.length !== 0
                                    && <Button href={project.githubRepo[1]} target="_blank" className="custom-colors m-2" style={{background : project.backgroundColor, color : project.textColor}}><i className="fab fa-github fa-2x"></i> {project.githubRepo[0]}</Button>
                                    }
                                    {project.otherLinks !== 0 &&
                                    project.otherLinks.map((otherLink) => (
                                        <Button href={otherLink[1]} target="_blank" className="custom-colors m-2" style={{background : project.backgroundColor, color : project.textColor}}><i className="fas fa-link fa-2x"></i> {otherLink[0]}</Button>
                                    ))
                                    }
                                </Col>
                            </Row>

                            {project.description.length !== 0 &&
                            <Container>
                            {project.description.map(paragraph => (
                                <p className="my-3 font-weight-bold txt-small">{paragraph}</p>
                                ))}
                            </Container>
                            }

                            {project.features.length !== 0 &&
                            <Container className="my-4">
                                <h6>Features</h6>
                                {/* <ul>
                                {project.features.map(feature => (
                                    <li><h5 className="txt-no-transform playfair-display">{feature}</h5></li>
                                    ))}
                                </ul> */}
                                {/* {project.features.map(feature => (
                                    <Badge pill className="custom-colors m-2" style={{background : project.backgroundColor, color : project.textColor, maxWidth : 'fit-content', wordBreak : 'break-word'}}>
                                        <h4 className="break-word txt-no-transform playfair-display font-weight-bold">{feature}</h4>
                                    </Badge>
                                ))} */}
                                {project.features.map(feature => (
                                    <Button disabled className="custom-colors m-1 pt-2 pb-1 px-2" style={{background : project.backgroundColor, color : project.textColor, borderRadius : '40px', border : '1px solid rgba(0,0,0, 0.5)'}}>
                                        <h5 className="break-word txt-no-transform copernicus-bold-font font-weight-bold p-0 m-0">{feature}</h5>
                                    </Button>
                                ))}
                            </Container>
                            }

                            {(project.image || project.images.length !== 0) ?

                                <Container fluid className="jumbotron justify-content-center align-items-center text-center">
                                    {/* Zoom an image info */}
                                    <h5 className="font-weight-bold txt-no-transform project-card-text" style={{color : '#1daecf'}}>
                                        <OverlayTrigger
                                            trigger="hover"
                                            key="top"
                                            placement="top"
                                            overlay={
                                            <Popover id={`popover-positioned-top`}>
                                                {/* <Popover.Title as="h3">{`Popover top`}</Popover.Title> */}
                                                <Popover.Content className="bg-secondary">
                                                <p className="lato font-weight-bold" style={{fontSize : '1rem'}}>Double tap to zoom in/out and drag within to move.</p>
                                                <p className="lato font-weight-bold" style={{fontSize : '1rem'}}>For small screens,pinch out gesture to zoom would be better option.</p>
                                                <p className="lato font-weight-bold" style={{fontSize : '1rem'}}>Some images may not zoom in.</p>
                                                </Popover.Content>
                                            </Popover>
                                            }
                                        >
                                            <i className="fas fa-info-circle">Zoom an Image?</i>
                                        </OverlayTrigger>
                                    </h5>
                                    {/* Card Image */}
                                    {project.image &&
                                    <Magnifier
                                        imageSrc={project.image}
                                        imageAlt={`A Screenshot of ${project.name}`}
                                        mouseActivation={MOUSE_ACTIVATION.DOUBLE_CLICK}
                                        touchActivation="doubleTap"
                                        fluid
                                        className="img-fluid my-5"
                                        style={{border : '1px solid rgba(0,0,0,0.1)'}}
                                    />
                                    // <Image src={project.image} fluid className="my-5"/>
                                    }

                                    {/* Rest of the images */}
                                    {project.images.length !== 0 
                                    &&
                                    project.images.map(image => (
                                        <Magnifier
                                            imageSrc={image}
                                            imageAlt={`A screenshot of ${project.name}`}
                                            mouseActivation={MOUSE_ACTIVATION.DOUBLE_CLICK}
                                            touchActivation="doubleTap"
                                            fluid
                                            className="img-fluid my-5"
                                            style={{border : '1px solid rgba(0,0,0,0.1)'}}
                                        />
                                        // <Image src={image} fluid className="my-5"/>
                                        ))}
                                </Container>

                            :   <h5 className="txt-no-transform text-center my-5 project-card-text" style={{color : '#1daecf'}}><i className="fas fa-frown-open"></i> Sorry, no screenshots are included for this project.</h5>

                            }

                            {/* Switch to next or previous project */}
                            {pIndex !== null &&
                            <Row className="text-center">
                                {projects[pIndex - 1] && 
                                <Col>
                                <h6 className="mb-2">Also See</h6>
                                <Button href="#projectModal" className="btn-light" onClick={() => { setPIndex(pIndex - 1); setProject(projects[pIndex - 1]); setShow(false); setShow(true) }}> <h1 className="txt-medium txt-no-transform"><i className="fas fa-arrow-left"></i> Previous Project</h1></Button>
                                <h4 className="txt-no-transform">{projects[pIndex - 1].name} - {projects[pIndex - 1].tagLine}</h4>
                                </Col>
                                }
                                {projects[pIndex + 1] && 
                                <Col>
                                <h6 className="mb-2">See Also</h6>
                                <Button href="#projectModal" className="btn-light" onClick={() => { setPIndex(pIndex + 1); setProject(projects[pIndex + 1]); }}> <h1 className="txt-medium txt-no-transform">Next Project <i className="fas fa-arrow-right"></i></h1></Button>
                                <h4 className="txt-no-transform">{projects[pIndex + 1].name} - {projects[pIndex + 1].tagLine}</h4>
                                </Col>
                                }
                            </Row>
                            }

                        </Container>
                    </Modal.Body>

                    <Modal.Footer className="m-0 p-0">
                        <Button className="btn-block" onClick={() => setShow(false)}>Close</Button>
                    </Modal.Footer>
                    
                    </>
                    }
                </Modal>
            </Row>
        </Container>
    )
}

export default Projects
